 .main {
    display: flex;
    flex-direction: row;
    height: 100vh;
    padding: 1vw;
  }
  
  .layout {
    display: flex;
    flex-direction: row;
    width: 100%;
  }
  
  .sidebar {
    width: 20%;
    background-color: rgb(166, 233, 233);
    height: 100vh;
    border-radius: 8pt;
  }
  
  .logo {
    height: 10vh;
    border-radius: 8pt;
    padding: 20px;
    font-weight: bolder;
  }
  
  .menu {
    height: 70vh;
    border-radius: 8pt;
    margin-top: 40pt;
  }
  
  .menu-item {
    margin-top: 10pt;
  }
  
  .menu-item i {
    font-size: 1.2rem;
    margin: 0 8pt;
    color: rgb(20, 20, 61);
  }
  
  .menu-item a {
    text-decoration: none;
    font-size: 1.2rem;
    color: rgb(20, 20, 61);
  }
  
  .active {
    background-color: rgb(194, 243, 243);
    padding: 10px;
    border-radius: 8px;
  }
  
  .active a,
  .active i {
    color: rgb(20, 20, 61);
  }
  
  .content {
    border-radius: 8px;
    height: 100vh;
    width: 80%;
    padding: 1vw;
  }
  
  .header {
    height: 10vh;
    background-color: rgb(189, 242, 242);
    border-radius: 8pt;
  }
  
  .header-content {
    padding: 8pt;
    margin: 0 15pt;
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  
  .header-content i {
    color: rgb(20, 20, 61);
    margin-right: 10pt;
    font-size: 1.4rem;
  }
  
  .header-content a {
    color: rgb(20, 20, 61);
    text-decoration: none;
    text-transform: uppercase;
    margin-right: 10pt;
    margin-left: 10pt;
    font-size: 1.2rem;
    font-weight: bold;
  }
  
  .item {
    margin-top: 10pt;
    height: 87vh;
    border-radius: 8pt;
    padding: 20px;
    /* background-color: rgb(187, 241, 241); */

  }
  
  /* Media queries */
  @media only screen and (max-width: 800px) {
    .layout {
      flex-direction: column;
    }
  
    .sidebar {
      width: 100%;
      height: auto;
      margin-bottom: 20pt;
    }
  
    .content {
      width: 100%;
    }
  
    .menu {
      height: auto;
    }
  
    .menu-item {
      margin-top: 5pt;
    }
  
    .header-content {
      justify-content: center;
    }
  
    .header-content a {
      margin: 0;
    }
  }
