*{
  padding: 0%;
  margin: 0%;
  box-sizing: border-box;
}

/* spinner */
.spinner{
height: 100vh;
display: flex;
align-items: center;
justify-content: center;
z-index: 9999;
background-color: rgba(128, 128, 128, 0.427)
;
}

/* form button */
.form-btn{
  width:10rem !important;
  margin-top: 1.8rem !important;
  background-color: rgb(13, 13, 56);
  color: white;
  font-weight: bold;
  border-radius: 8px;
}